const ATTR_EXCEPTIONS = [
  'role',
  'dataset',
  'd',
  'r',
  'cx',
  'cy',
  'width',
  'height',
  'viewBox',
  'fill',
]
const SVG_NAMESPACE = "http://www.w3.org/2000/svg"
const PROPS_TYPES = ['string', 'number', 'object']
const NO_EL_TYPES = ['string', 'number', 'boolean']
const CLASS_NAME_TYPES = [ 'string', 'object' ]
const appendText = (el, text) => (
  el && (text || text === false) &&
    el.appendChild(document.createTextNode(text))
)
  
const appendArray = (el, children) => (
  children
    .map((child) => (
      Array.isArray(child)
        ? appendArray(el, child)
        : child instanceof window.Element
          ? el.appendChild(child)
          : NO_EL_TYPES.includes(typeof child)
            ? appendText(el, child)
            : null
    ))
)

const setStyles = (el, styles) => (
  !styles 
    ? el.removeAttribute(`styles`)
    : Object
      .keys(styles)
      .map(styleName => (
        styleName in el.style
          ? ( el.style[styleName] = styles[styleName] )
          : console.warn(
            `${styleName} is not a valid style for a <${el.tagName.toLowerCase()}>`
          )
      ))
)

const setDataAttributes = (el, dataAttrs) => (
  Object
    .keys(dataAttrs)
    .map((dataAttr) => {
      el.setAttribute(`data-${dataAttr}`, dataAttrs[dataAttr])
    })
)


const addList = (list, name) => list.indexOf(` ${name} `) === -1 && (list += ` ${name} `)

const buildClassName = (el, prop, value) => {
  const valueType = typeof value
  if(prop !== 'className' || CLASS_NAME_TYPES.indexOf(valueType) === -1)
    return el

  if(valueType === 'string') return (el[prop] = value)
  
  Array.isArray(value) && (
    value.reduce((list, name, index) => {
      if(typeof name === 'string') addList(list, name)
      else if (Array.isArray(name))
        name.map(subName => ( addList(list, subName) ) )

      return index === value.length -1
        ? (el[prop] = list.trim())
        : list
    }, '')
  )
}

const mapProps = (el, props) => (
  Object
    .keys(props)
    .map(prop => {
      const value = props[prop]
      let custom = false
      if(props[prop] === undefined || props[prop] === null) return

      if(prop === 'for') prop = 'htmlFor'
      if(prop === 'class') prop = 'className'
      if(prop.indexOf('data-') === 0) custom = true
      if(prop.indexOf('on') === 0) prop = prop.toLowerCase()
      
      
      if (!custom && !(prop in el) && !ATTR_EXCEPTIONS.includes(prop))
        return null

      switch(prop){
        case 'style':
          return setStyles(el, value)
        case 'dataset':
          return setDataAttributes(el, value)
        case 'htmlFor':
          return (el[prop] = value)
        case 'className':
          return buildClassName(el, prop, value)
        default:
          return custom
            ? el.setAttribute(prop, value)
            : typeof value === `function` && prop.indexOf('on') === 0
              ? (el[prop] = value)
              : value && el.setAttribute(prop, value)
      }
    })
)

const isSvg = type => ([`path`, `svg`, `circle`].includes(type))

const makeProps = (el, props, propsType) => (
  propsType === `string` || propsType === `number`
    ? appendText(el, props)
    : Array.isArray(props)
      ? appendArray(el, props)
      : props instanceof window.Element
        ? el.appendChild(props)
        : mapProps(el, props)  
)

const makeEl = type => (
  isSvg(type)
    ? document.createElementNS(SVG_NAMESPACE, type)
    : document.createElement(type)
)

const eR = (type, props, ...children) => {
  const el = makeEl(type)

  const propsType = typeof props
  PROPS_TYPES.includes(propsType) &&
    makeProps(el, props, propsType)

  children && 
    appendArray(el, children)

  return el
}

const elements = [
  'a',
  'b',
  'br',
  'button',
  'circle',
  'footer',
  'form',
  'div',
  'h1',
  'h2',
  'h3',
  'h4',
  'h5',
  'h6',
  'header',
  'i',
  'input',
  'label',
  'link',
  'li',
  'nav',
  'ol',
  'option',
  'p',
  'path',
  'select',
  'span',
  'style',
  'svg',
  'textarea',
  'u',
  'ul',
].reduce((els, type) => (
  (els[type] = (...args) => eR(type, ...args)) && els
), {})

export {
  elements,
  eR
}